:root {
  --font-space: "Space Grotesk","Inter", sans-serif;
}

:root {
  --background: #151515;
  --underline: #6C1BF0;
  --interactive-bg: #242424;
  --text: #f8f8f8;
  --text2: #d9d9d9;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

a {
  color: inherit;
}

h1, h2, h3, p {
  margin: 0;
}

h1, h2, h3, a {
  font-family: var(--font-space);
  color: var(--text);
}

a, a:visited {
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.625rem;
  letter-spacing: 0.142856875rem;
  text-transform: uppercase;
  text-decoration: none;
}

a:hover, a:active {
  color: var(--underline);
  text-decoration: underline solid 2px var(--underline);
  text-underline-offset: 0.625rem;
  text-underline-position: under;
}

h1 {
  font-size: 5.5rem;
  font-weight: 700;
  line-height: 6.5rem;
  letter-spacing: -0.15625rem;
}
@media (max-width: 768px) {
  h1 {
    font-size: 4.6875rem;
  }
}

h2 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 3.5rem;
  letter-spacing: -0.09375rem;
}
@media (max-width: 768px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
}

p, span {
  font-family: var(--font-space);
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.75rem;
  color: var(--text2);
}

html,
body {
  overflow-x: hidden;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  /*   padding: rem(39) rem(200) rem(92) rem(200); */
  background-color: var(--background);
  /*  @include breakpoint-down(medium) {
    padding: rem(45) rem(18) 0 rem(18);
  }

  @include breakpoint-down(small) {
    padding: rem(20) rem(16) 0 rem(16);
  } */
}

header,
.hero,
.skills,
.projects {
  padding: 2.4375rem 12.5rem 5.75rem 12.5rem;
}
@media (max-width: 768px) {
  header,
  .hero,
  .skills,
  .projects {
    padding: 2.8125rem 1.125rem 0 1.125rem;
  }
}
@media (max-width: 640px) {
  header,
  .hero,
  .skills,
  .projects {
    padding: 1.25rem 1rem 0 1rem;
  }
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header img {
  width: 2.8125rem;
  height: 2.8125rem;
  z-index: 1;
}

.projects {
  padding-top: 6.9375rem;
}
.projects .projects__title {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
.projects .projects__title .name_flex__contact {
  justify-self: end;
}
.projects .projects__title .projects__rings {
  position: absolute;
  left: 92%;
  bottom: 3%;
  z-index: -1;
  width: 33.125rem;
  height: auto;
}
@media (max-width: 768px) {
  .projects .projects__title .projects__rings {
    right: 57%;
    top: 17%;
  }
}
.projects__card {
  padding-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(3, 1fr);
  gap: 4.3125rem 1.875rem;
}
.projects__card__wrapper__picture {
  display: flex;
}
.projects__card__wrapper__picture picture {
  position: relative;
}
.projects__card__wrapper__picture picture img {
  display: block;
  width: 34.0625rem;
  height: auto;
}
.projects__card__wrapper__picture picture .projects__card__wrapper__picture__tags {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  gap: 3.75rem;
  transition: ease-in-out 0.3s;
}
.projects__card__wrapper__picture picture .projects__card__wrapper__picture__tags:hover {
  opacity: 1;
}
.projects__card__wrapper__text {
  display: flex;
  flex-direction: column;
  gap: 0.4375rem;
  padding-top: 1.25rem;
}
.projects__card__wrapper__text__lang {
  display: flex;
  gap: 1.125rem;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding-top: 6.5rem;
}
.skills__names {
  display: grid;
  grid-template-columns: 0.5fr 1fr 0.5fr;
  grid-template-rows: 1fr;
  gap: 8.75rem;
  justify-items: center;
}
.skills__names .skill {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.skills__names .skill .experience {
  padding-top: 0.875rem;
  text-decoration: underline solid var(--underline);
  text-underline-offset: 0.125rem;
  text-underline-position: under;
}

.contact {
  width: 100%;
  margin-top: 7.3125rem;
  padding: 5.25rem 10.3125rem 0 10.3125rem;
  background-color: var(--interactive-bg);
}
.contact .bottom-border {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 13.75rem;
}
@media (max-width: 768px) {
  .contact .bottom-border {
    right: 57%;
    top: 17%;
  }
}
.contact .bottom-border .contact__rings {
  position: absolute;
  left: -30%;
  bottom: 3%;
  z-index: 1;
  width: 33.125rem;
  height: auto;
}
.contact .bottom-border .contact__left {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 75%;
}
.contact .bottom-border .contact__left__text h1 {
  padding-bottom: 2.25rem;
}
.contact .bottom-border .contact__right .contact__control input,
.contact .bottom-border .contact__right .contact__control textarea {
  width: 100%;
  padding-block: 1rem;
  padding-inline: 1.5rem;
  border: none;
  border-bottom: 1px solid var(--text);
  background: transparent;
  font-size: 1rem;
  line-height: 1.625;
  color: var(--text);
}
.contact .bottom-border .contact__right .contact__control > *::placeholder {
  color: var(--text);
  opacity: 0.5;
  text-transform: uppercase;
}
.contact .bottom-border .contact__right .contact__control textarea {
  margin-block-end: 2rem;
}
.contact .bottom-border .contact__right .contact__control.align-right {
  display: flex;
  justify-content: flex-end;
}
.contact .bottom-border .contact__right .contact__control.align-right button {
  font-family: var(--font-space);
  color: var(--text);
  font-weight: 700;
  line-height: 1.625rem;
  letter-spacing: 0.142856875rem;
  text-transform: uppercase;
  text-decoration: none;
  background-color: transparent;
  border: none;
}
.contact .bottom-border .contact__right .contact__control.align-right button:hover {
  color: var(--underline);
  text-decoration: underline solid 2px var(--underline);
  text-underline-offset: 0.625rem;
  text-underline-position: under;
}
.contact .bottom-border .contact__right .contact__invalid-icon {
  display: none;
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 50%;
  right: 0;
  translate: -50% -50%;
}
.contact .bottom-border hr {
  height: 0.0625rem;
  background-color: var(--text);
  width: 100%;
  margin-top: 5.75rem;
}

.hero {
  padding-top: 4.4375rem;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 768px) {
  .hero {
    padding-top: 1rem;
  }
}
.hero__wrapper .hero__image {
  position: absolute;
  top: 0;
  left: 68.5%;
  translate: -50%;
  max-width: 27.8125rem;
  height: auto;
  z-index: -1;
}
@media (max-width: 1200px) {
  .hero__wrapper .hero__image {
    width: 16.1875rem;
    left: 80%;
  }
}
@media (max-width: 768px) {
  .hero__wrapper .hero__image {
    width: 16.1875rem;
    left: 80%;
  }
}
.hero__wrapper .hero__rings {
  position: absolute;
  right: 77%;
  top: 15%;
  z-index: -1;
  width: 33.125rem;
  height: auto;
}
@media (max-width: 768px) {
  .hero__wrapper .hero__rings {
    right: 57%;
    top: 17%;
  }
}
.hero__wrapper .hero__circle {
  position: relative;
  top: 21.875rem;
  left: 54%;
}
@media (max-width: 768px) {
  .hero__wrapper .hero__circle {
    display: none;
  }
}
.hero__wrapper .name_flex {
  display: flex;
  flex-direction: column;
  justify-content: start;
}
.hero__wrapper .name_flex .underline span {
  font-size: 5.5rem;
  color: var(--text);
  text-decoration: underline solid var(--underline);
  text-underline-offset: 0.125rem;
  text-underline-position: under;
}
.hero__wrapper .name_flex .info {
  padding-top: 3.875rem;
  width: 45%;
}
.hero__wrapper .name_flex__contact {
  padding-top: 4.0625rem;
  width: fit-content;
}
.hero__wrapper .name_flex hr {
  height: 0.0625rem;
  background-color: var(--text);
  width: 100%;
  margin-top: 13.6875rem;
}